const storageNamespace = 'acies';

export function readFromStorage(key: string) {
  try {
    const item = localStorage.getItem(`${storageNamespace}:${key}`);
    try {
      // Attempt to parse as JSON
      if (item !== null) return JSON.parse(item);
    } catch (err) {
      // Fail silently
    }
    return item;
  } catch (err) {
    // Commonly fails if storage is disabled or using an old browser
    console.error(err);
  }
  return null;
}

export function writeToStorage(key: string, value: any) {
  try {
    localStorage.setItem(`${storageNamespace}:${key}`, value);
  } catch (err) {
    console.error(err);
  }
}
