<template>
  <div
    class="toggle"
    :data-theme="theme"
    :data-enter-theme="enterTheme"
    :data-prev-theme="prevTheme"
    @click="toggle"
    @mouseenter="enterTheme = theme"
  >
    <icon class="sun" name="wb_sunny" size="l" />
    <icon class="cloud" name="cloud" size="l" />
    <div class="label">
      <span v-if="theme === 'dark'">Shine some light!</span>
      <span v-if="theme === 'light'">Call upon darkness!</span>
    </div>
  </div>
</template>

<script>
import { readFromStorage, writeToStorage } from '@/utils';

export default {
  data: () => ({
    theme: readFromStorage('theme') || 'dark',
    enterTheme: null,
    prevTheme: null,
  }),
  watch: {
    theme: {
      handler(next, prev) {
        document.documentElement.classList.add(next);
        if (prev) document.documentElement.classList.remove(prev);
        this.prevTheme = prev;
        this.enterTheme = null;
      },
      immediate: true,
    },
  },
  methods: {
    toggle() {
      if (this.theme === 'dark') {
        this.theme = 'light';
      } else {
        this.theme = 'dark';
      }
      writeToStorage('theme', this.theme);
    },
  },
};
</script>

<style lang="scss" scoped>
// Container
.toggle {
  position: absolute;
  z-index: calc(var(--z-top) + 10);
  top: var(--size-m);
  right: var(--size-m);
  width: var(--size-xxxl);
  height: var(--size-xxxl);
  cursor: pointer;
  transition: color var(--speed-default) var(--ease-out-back);
  user-select: none;
  color: var(--color-accent-bg1);

  &:hover {
    color: var(--color-accent-bg1);
  }
}

// Sun
.sun {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

// Cloud
.cloud {
  position: absolute;
  top: 0;
  right: 0;
  overflow: visible;
  filter: drop-shadow(0 -2px 6px var(--color-accent-bg2));
  pointer-events: none;
  transition: transform var(--speed-default) var(--ease-out-back),
    opacity var(--speed-default) var(--ease-default);
}
.toggle[data-theme="light"],
.toggle[data-prev-theme="dark"]:hover,
.toggle[data-theme="dark"][data-enter-theme="dark"]:hover {
  .cloud {
    transform: translate(50%, -10%);
  }
}
.toggle[data-theme="dark"],
.toggle[data-prev-theme="light"]:hover,
.toggle[data-theme="light"][data-enter-theme="light"]:hover {
  .cloud {
    transform: translate(25%, -10%);
  }
}
.toggle:not(:hover) {
  .cloud {
    opacity: 0;
  }
}

// Label
.label {
  position: absolute;
  top: 50%;
  right: calc(100% + var(--size-xs));
  transform: translate(var(--size-xxs), -50%) scale(0.9);
  border-radius: var(--size-l);
  white-space: nowrap;
  font-size: var(--font-size-s);
  padding: var(--size-xxxs) var(--size-m);
  background-color: var(--color-fg1);
  color: var(--color-bg1);
  opacity: 0;
  transition: transform var(--speed-default) var(--ease-out-back),
    opacity var(--speed-default) var(--ease-default);
  pointer-events: none;
}
.toggle:hover .label {
  transform: translate(0%, -50%) scale(1);
  opacity: 1;
}
</style>
